<template>
	<div class="large-deposit-draw-draw">
		<mt-cell title="收款账户：" :value="detail.account1"></mt-cell>
		<mt-cell title="付款人名称：" :value="detail.account1"></mt-cell>
		<mt-cell title="存单本金：" :value="detail.account1"></mt-cell>
		<mt-cell title="提取支取利息：" :value="detail.account1"></mt-cell>
		<mt-cell title="本息合计：" :value="detail.account1"></mt-cell>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="page-part">
      		<mt-field class="code" label="验证码：" placeholder="输入验证码" :attr="{ maxlength: 6 }">
        		<mt-button type="primary">获取验证码</mt-button>
      		</mt-field>
    	</div>
    	<div class="field-warn">
    		提示：验证码为6位数字
    	</div>
		<div class="field-action">
            <submit-button @submit="submit" :submitStatus="submitStatus" value="提交"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default{
	data() {
		return {
            detail: {
    			account1: '',
    			account2: ''
            },
			code: '',
            submitStatus: false,
            cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
    watch: {
        'detail.account1': function(val) {
            // if(val){
                this.submitStatus = true;
            // }else{
            //     this.submitStatus = false;
            // }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account2 = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.submitDrawProduct();
        },
        back() {
        	history.back();
        },
        getSureDrawProductDetail() {
            service.getSureDrawProductDetail()
            .then(data => {
                this.detail = data.data.data;
            });
        },
        submitDrawProduct() {
            service.submitDrawProduct()
            .then(data => {
                this.$router.push({name: 'LargeDepositDrawSuccess'});
            });
        }
	},
	created() {
		this.getSureDrawProductDetail();
	}
};

</script>

<style lang="scss">
.large-deposit-draw-draw{
	.code{
		.mint-field-other{
			button{
				height: 70%;
				margin-top: 6%;
				margin-right: 16px;
			}
		}
	}
}
</style>


