<template>
	<div class="large-deposit-buy-list">
		<a @click="detail" v-for="item in list" href="javascript:;" class="buy-list-item">
			<div class="item-title">序号{{item.CP_ISSUE_ID}}</div>
			<div class="item-contain">
				<div class="item-label">
					<label>产品名称</label>
					<p>{{item.CP_PRODUCT_NM}}</p>
				</div>
				<div class="item-label">
					<label>剩余额度</label>
					<p>{{item.CP_US_AM}}</p>
				</div>
				<div class="item-label">
					<label>利率</label>
					<p>{{item.RATE}}</p>
				</div>
				<div class="item-label">
					<label>期限</label>
					<p>{{item.CP_PROD_DAY}}</p>
				</div>
			</div>
		</a>
		<div class="field-action">
            <mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default{
	data() {
		return {
			list: [],
			details: {}
		};
	},
	methods: {
		detail(id){
            this.$router.push({name: 'LargeDepositBuyDetail'});
		},
		back() {
        	history.back();
        },
        getBuyProductList(detail) {
        	service.getBuyProductList({
        		ACCOUNT: detail.account,
        		STATUS: detail.status,
        		PERIOD: detail.period,
        		ORDER_TYPE: detail.order
        	})
        	.then(data => {
        		this.list = data.data.data;
        	}, err => {
        		console.log(err);
        	});
        }
	},
	created() {
		this.details = Object.assign({}, this.$store.state.largeDepositBuy.detail);
		this.getBuyProductList(this.detail);
	}
};

</script>

<style lang="scss">

.large-deposit-buy-list{
	.buy-list-item{
		display: block;
		background-color: #fff;
		padding: 16px;
		border-top: 1px solid #d9d9d9;
	}
	.buy-list-item + .buy-list-item{
		border-bottom: 1px solid #d9d9d9;
	}
	.buy-list-item:last-child{
		border-bottom: 1px solid #d9d9d9;
	}
	.item-title{
		font-size: 14px;
		margin-bottom: 16px;
	}
	.item-label{
		display: inline-block;
		width: 23%;
		text-align: center;
		label{
			font-size: 14px;
		}
	}
	.item-label:nth-child(2){
		margin-right: 1%;
		margin-left: 1.5%;
	}
	.item-label:nth-child(3){
		margin-right: 1.5%;
		margin-left: 1%;
	}
}

</style>


