<template>
	<div class="transaction-detail-list">
		<mt-cell class="card-number" title="卡号">
  			<span class="number">123456789</span>
		</mt-cell>
        <infinite-scroll v-if="list.length > 0" ref="infiniteScroll" :data="list" :hasMoreData="hasMoreData" @pullup="pullup" >
            <div class="detail-list cell-part">
                <mt-cell v-for="item in list" :key="item" @click.native="toDetail" :title="item.type" :label="item.time" class="list-item" is-link>
                    <p class="item-p">
                        <span :class="{'money-add': item.money1 >= 0, 'money-reduce': item.money1 < 0}">{{item.money1 < 0 ? '' : '+' }}{{item.money1 | formatNumber(2)}}</span><br>
                        <span class="balance">余额：{{item.money2 | formatNumber(2)}}元</span>
                    </p>
                </mt-cell>
            </div>
        </infinite-scroll>
        <div class="no-record" v-else>
            <div class="bg">
                <div class="bg-pic"></div>
                <p class="bg-msg">该账户下暂无交易记录</p>
            </div>
            <div class="fixed-footer">
                <mt-button @click.native="back" size="large" type="primary">返回</mt-button>
            </div>
        </div>
	</div>
</template>

<script>

import service from '@/services/bank/transaction-detail';

export default {
    data() {
        return {
            list: [],
            hasMoreData: true,
            count: 1
        };
    },
    methods: {
        toDetail() {
            this.$router.push({name: 'TransactionDetailDetail'});
        },
        pullup (){
            console.log('pullup');
            console.log(this.loadingMore);
            if( this.loadingMore ) {
                return;
            }
            if( this.count >= 3 ) {
                this.hasMoreData = false;
                return;
            }
            console.log('loadingMore');
            this.loadingMore = true;
            this.count ++;
            console.log(this.count);
            setTimeout( () => {
                let list = this.list;
                this.list = this.list.concat( list );
                this.loadingMore = false;
            }, 2000);
        },
        getList(account) {
        	service.getTransactionList({
        		account: account
        	}).then(data => {
        		this.list = data.data.data;
        	});
        },
        back() {
            history.back();
        }
    },
    created() {
    	this.getList(this.$route.params.account);
    }
};
</script>

<style lang="scss">
.transaction-detail-list{
    height: 100%;
	.card-number{
		position: fixed;
        top:0;
        z-index: 1;
        width: 100%;
        padding-top: 2px;
        padding-bottom: 2px;
        height: 102px;
        @include image-set('../../../assets/img/bank/transaction/transaction');
        .number{
            font-size: 15px;
            color: #444857;
            font-weight: normal;
            letter-spacing: 2.3px;
            font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
        }
	}
	.detail-list{
		padding-top: 102px;
        height: 100%;
        overflow: auto;
	}
    .list-item{
        padding: 11px 0;
        height: 122px;
    }
	.item-p{
		padding-right: 0;
		text-align: right;
	}
    .money-add{
        font-size: 20px;
        color: #ff942f;
    }
    .money-reduce{
        font-size: 20px;
        color: #46c300;
    }
	.balance{
		display: block;
        padding-top: 12px;
        font-size: 13px;
        color: #b5b5b5;
	}
    .mint-cell-title{
        .mint-cell-label{
            color: #b5b5b5;
            font-size: 13px;
        }
    }
    .mint-cell:after{
        left: 0;
    }
    .no-record{
        .bg{
            margin-top: 242.2px;
            text-align: center;
            .bg-pic{
                margin: auto;
                width: 177.8px;
                height: 243.8px;
                background-size: 100% 100%;
                @include image-set('../../../assets/img/bank/transaction/no-record');
            }
            .bg-msg{
                margin-top: 40px;
                font-size: 17px;
                color: #666666;
            }
        }
    }
}
</style>



