<template>
    <div class="transaction-detail">
        <div class="field-part">
            <account-select title="卡号" :value=" dataForm.PAYMENT_ACCOUNT || '请选择卡号' " 
                :balance="selectorPaymentAccount.checked.BALANCE"
                type="complex" @click.native=" showSelectorPaymentAccount() ">
            </account-select>
        </div>
        <div class="field-part">
            <cell-transfer-money 
                title="转账金额" placeholder="请输入转账金额" 
                :totalMoney="selectorPaymentAccount.checked.BALANCE" twoValidate="show" v-model="dataForm.TRAN_AMOUNT">
            </cell-transfer-money>
            <span class="pattern-span" @click="showSelectorTransferAging()">{{dataForm.patternName}}，<label>更换到账时间</label></span>
        </div>
        <div class="field-part field-group pattern-span-next">
            <mt-field label="收款人" placeholder="收款人"  v-model=" dataForm.PAYEE_NAME " disableClear>
            </mt-field>
            <mt-field label="收款账户" placeholder="收款账号" type="number" v-model=" dataForm.PAYEE_ACCOUNT " disableClear></mt-field>
            <router-link to="/selector/payee" class="btn-selector btn-seletor-payee">
                <i class="icon"></i>
            </router-link>
        </div>
        <div  class="field-part">
            <mt-cell title="收款银行" href="#/selector/bank"  :value=" dataForm.bankName || '请选择银行' " is-link>
            </mt-cell>
            <mt-cell title="开户地" href="#/selector/bank-city"  :value=" dataForm.bankCity || '请选择开户地址' " is-link>
            </mt-cell>
            <mt-cell title="分支行" href="#/selector/bank-branch"  :value=" dataForm.bankBranch || '请选择支行' " is-link>
            </mt-cell>
        </div>
        <!-- <div class="field-part">
            <mt-cell title="分支行" href="#/selector/bank-branch"  :value=" dataForm.bankBranch || '请选择支行' " is-link>
            </mt-cell>
        </div> -->
        <div class="field-part field-remark">
            <mt-field label="附言" placeholder="选填" v-model="dataForm.SENDMENO" :attr="{ maxlength: 70 }"></mt-field>
        </div>
        <span class="field-remark-dest">转账附言70字以内</span>
        <div class="field-action">
            <mt-button @click="getFee()" :disabled="!submitStatus" size="large" type="primary">下一步</mt-button>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
        <selector-transfer-aging ref="selectorTransferAging" :options=" selectorTransferAging " ></selector-transfer-aging>
    </div>
	
</template>

<script>

// import { mapGetters, mapActions, mapState } from 'vuex';
import * as service from '@/services/public';
import * as serviceTransfer from '@/services/bank/external-transfer';

export default {
    data() {
        return {
            submitStatus: false,
            dataForm: {
                paymentAccountList: [],
                checked: {},
                patternName: '普通到账',
                TRAN_TYPE: '1', // 转账方式
                PAYEE_NAME: '', // 收款户名
                PAYEE_ACCOUNT: '', // 收款账号
                PAYEE_BANK_NO: '', // 收款银行编号
                PAYMENT_ACCOUNT: '', // 付款账户
                TRAN_CURRENCY: '0', // 付款币种 0-人民币
                TRAN_AMOUNT: '', // 转账金额
                TRAN_FEE: '', // 转账手续费
                PAY_PWD: '',  // 交易密码
                SENDMENO: '', // 附言
                payeeBankName: '',
                bankName: '',
                bankCity: '',
                bankBranch: ''
            },
            selectorPaymentAccount: {
                title: '选择付款账号',
                type: 'payment',
                checked: {},
                showAddBtn: false,
                showWarn: true,
                showBankName: false,
                accountList: []
            },
            selectorTransferAging: {
                title: '选择到账时间',
                checked: '',
                instant: true,
                general: true,
                morrow: true
            }
        };
    },
    created(){
        // this.getBankCode();
        this.dataForm = Object.assign({}, this.$store.state.externalTransfer.search);
        this.dataForm = Object.assign(this.dataForm, this.$store.getters.transactionDemo);
        if(!this.dataForm.PAYMENT_ACCOUNT){
            // flagType 目前没数据 后面改为3
            service.getCommonAccountList({'flagType': '0'}).then((res) => {
                this.selectorPaymentAccount.accountList = res.LIST;
                this.selectorPaymentAccount.checked = res.LIST[0];
                this.dataForm.paymentAccountList = res.LIST;
                this.dataForm.checked = res.LIST[0];
                this.dataForm.PAYMENT_ACCOUNT = res.LIST[0].ACCOUNT;
            }, (err) => {
                console.log( err );
            });
        }else{
            this.selectorPaymentAccount.accountList = this.dataForm.paymentAccountList;
            this.selectorPaymentAccount.checked = this.dataForm.checked;
            this.dataForm.PAYMENT_ACCOUNT = this.dataForm.checked.ACCOUNT;
        }
    },
    mounted(){
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            var _error = [];
            // if( this.$validate.isEmpty( _data.PAYMENT_ACCOUNT ) ) {
            //     _error.push( '银行卡号不能为空。' );
            // }
            // if( _error.length > 0 ) {
            //     this.$toast(_error[0]);
            //     return;
            // }
    		var params = {
    			path: '/external-transfer/pay',
    			query: {
    				id: _data.PAYMENT_ACCOUNT
    			}
    		};
           this.$router.push(params);
        },
        getFee () {
            serviceTransfer.getExternalTransferFee({
                // 'TRANCODE': '021003',
                // 'TRAN_AMT': this.dataForm.TRAN_AMOUNT,
                // 'ACCOUNT': this.dataForm.PAYMENT_ACCOUNT,
                // 'ACCT_NAME': 'test',
                // 'COR_INFO': this.dataForm.PAYEE_ACCOUNT,
                // 'COR_NAME': this.dataForm.PAYEE_NAME,
                // 'BANKCODE': this.dataForm.PAYEE_BANK_NO
                'TRANCODE': '021001',
                'TRAN_AMT': '0.1',
                'ACCOUNT': '6215268408400023818',
                'ACCT_NAME': '邵珠利',
                'COR_INFO': '6217788314500045866',
                'COR_NAME': '测试账户',
                'BANKCODE': ''
            }).then((res) => {
                this.dataForm.TRAN_FEE = res.FEE;
                this.search();
            }, (err) => {
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        },
        getBankCode (){
            serviceTransfer.getExternalTransferBankCode({
                'CITYCODE': '3720',
                'BANKCODE': '105',
                'BANK_NAME': '阜阳'
            }).then((res) => {
                console.log(res);
            }, (err) => {
                console.log( err );
            });
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.PAYMENT_ACCOUNT = data.item.ACCOUNT;
                this.dataForm.checked = data.item;
            }, ( data ) => {});
        },
        showSelectorTransferAging() {
            this.$refs.selectorTransferAging.show().then(( data ) => {
                this.selectorTransferAging.checked = data.item;
                this.dataForm.TRAN_TYPE = data.item.code;
                this.dataForm.patternName = data.item.text;
            }, ( data ) => {});
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.TRAN_AMOUNT && item.PAYEE_NAME && item.PAYEE_ACCOUNT){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            var d = this.dataForm;
            this.$store.dispatch('updatePayee', {"name": d.PAYEE_NAME, "account": d.PAYEE_ACCOUNT, "bankName": d.payeeBankName});
            this.$store.dispatch('updateExternalTransferSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearExternalTransferSearch');
            this.$store.dispatch('clearPayee');
            this.$store.dispatch('clearBank');
            this.$store.dispatch('clearBankCity');
            this.$store.dispatch('clearBankBranch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.field-remark {
    margin-top: 40px;
}
.btn-seletor-payee
{
    background-color: #fff;
    .icon
    {
        background-image: url('../../../../assets/img/selector/icon-address-book.png');
    }
}
</style>



