<template>
	<div class="large-deposit-search">
		<div class="field-action">
			<mt-button @click.native="toNext('LargeDepositSearchCustom')" type="primary">客户持有信息查询</mt-button>
		</div>
		<div class="field-action">
			<mt-button @click.native="toNext('LargeDepositSearchTransaction')" type="primary">大额存单交易查询</mt-button>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

export default{
	data() {
		return {
		};
	},
	methods: {
        toNext(name) {
        	this.$router.push({name: name});
        },
        back() {
        	history.back();
        }
	}
};

</script>

<style lang="scss">
.large-deposit-search{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


