<template>
    <div>
        <div class="success-info">
            <tip-page tipTitle="转账成功" tipMsg="实时转账"></tip-page>
            <transfer-success-detail 
                :payer='accountsDetail.account' payerAccount='' payerBank='' 
                :payee='accountsDetail.payeeName' :payeeAccount='accountsDetail.payeeAccount' :payeeBank='accountsDetail.payeeBankName' 
                :payeeMoney='accountsDetail.money' :remark='accountsDetail.remark' transferType='6'></transfer-success-detail>
        </div>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toIndex()">返回首页</mt-button>
        </div>
        <div class="cell-action inline">
            <mt-button size="large" @click = "toAccountIndex()">继续转账</mt-button>
            <mt-button size="large" @click = "toInternalTransferIndex()">返回转账首页</mt-button>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            accountsDetail: []
        };
    },
    created() {
        this.accountsDetail = Object.assign({}, this.$store.state.twoDimensionCode.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        toIndex() {
            this.$router.push('/');
        },
        toAccountIndex() {
            this.$ls.setItem("two-dimension-code-back", "code");
            // this.$router.go(-3);
            this.$router.push('/accounts/index');
        },
        toInternalTransferIndex() {
            this.$router.push('/internal-transfer/index');
        }
    }
};
</script>


<style lang="scss">
.success-info {
    background: #fff;
}
</style>



