<template>
	<div class="large-deposit-transfer-in-detail">
		<mt-cell title="收款人账号：" :value="detail.number"></mt-cell>
		<mt-cell title="收款人名称：" :value="detail.company"></mt-cell>
		<mt-cell title="大额存单账户：" :value="detail.money1"></mt-cell>
		<mt-cell title="发行编号：" :value="detail.money2"></mt-cell>
		<mt-cell title="产品名称：" :value="detail.rate"></mt-cell>
		<mt-cell title="期限：" :value="detail.date"></mt-cell>
		<mt-cell title="付息方式：" :value="detail.date2"></mt-cell>
		<mt-cell title="是否允许转让：" :value="detail.status"></mt-cell>
		<mt-cell title="存单利率：" :value="detail.status"></mt-cell>
		<mt-cell title="提前支取利率：" :value="detail.status"></mt-cell>
		<mt-cell title="起息日期：" :value="detail.status"></mt-cell>
		<mt-cell title="到期日期：" :value="detail.status"></mt-cell>
		<mt-cell title="存单本金：" :value="detail.status"></mt-cell>
		<mt-cell title="提前支取本息合计：" :value="detail.status"></mt-cell>
		<mt-cell title="到期本息合计：" :value="detail.status"></mt-cell>
		<mt-field label="出售金额：" placeholder="请输入出售金额" type="number"></mt-field>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default {
	data() {
		return {
			detail: {
				liveAccount: '',
				keepAccount: '',
				money: '',
				type: '',
				status: '',
				date: '',
				date1: '',
				date2: '',
				rate: ''
			}
		};
	},
	methods: {
		submit() {
			this.$router.push({name: 'LargeDepositTransferInTransfer'});
		},
		back() {
			history.back();
		},
		getInProductDetail(account) {
			service.getInProductDetail({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getInProductDetail(this.$route.params.account);
	}
};

</script>


