<template>
	<div class="large-deposit-buy-detail">
		<mt-cell title="发行编号：" :value="detail.CP_ISSUE_ID"></mt-cell>
		<mt-cell title="发行机构：" :value="detail.CP_SB_NO"></mt-cell>
		<mt-cell title="产品名称：" :value="detail.CP_PRODUCT_NM"></mt-cell>
		<mt-cell title="起存金额：" :value="detail.CP_MI_AM"></mt-cell>
		<mt-cell title="递增金额：" :value="detail.CP_IS_AM"></mt-cell>
		<mt-cell title="提前支取利率：" :value="detail.CP_WD_RATE"></mt-cell>
		<mt-cell title="存单期限：" :value="detail.CP_PROD_DAY"></mt-cell>
		<mt-cell title="发行开始日期：" :value="detail.CP_ST_DT"></mt-cell>
		<mt-cell title="发行结束日期：" :value="detail.CP_ED_DT"></mt-cell>
		<mt-cell title="付息方式：" :value="detail.CP_IN_PM"></mt-cell>
		<mt-cell title="是否允许转让：" :value="detail.CP_TR_FG"></mt-cell>
		<mt-cell title="存单利率：" :value="detail.RATE"></mt-cell>
		<mt-cell title="卡号：" :value="detail.account"></mt-cell>
        <mt-field v-model="buymoney" label="购买金额：" type="number"></mt-field>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default {
	data() {
		return {
			detail: {
				number: '',
				company: '',
				name: '',
				money1: '',
				money2: '',
				rate1: '',
				date: '',
				date1: '',
				date2: '',
				type: '',
				transfer: '',
				rate2: '',
				account: ''
			},
			buymoney: ''
		};
	},
	methods: {
		submit() {
			this.$router.push({name: 'LargeDepositBuyBuy'});
		},
		back() {
			history.back();
		},
		getBuyProductDetail(account) {
			service.getBuyProductDetail({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.getBuyProductDetail(this.$route.params.account);
	}
};

</script>


