import ajax from '@/services/ajax';
import api from '@/services/api';

export function getExternalTransferIndex(data) {
    return ajax.postAfterLoginQuery( '021003', data, api.getExternalTransferIndex );
}

export function getExternalTransferFee(data) {
    return ajax.postAfterLoginQuery( '020201', data, api.getExternalTransferFee );
}

export function getExternalTransferBankCode(data) {
    return ajax.postAfterLoginQuery( '010201', data, api.getExternalTransferBankCode );
}

export function getExternalTransferPay(data) {
    return ajax.postAfterLoginUpdate( '021003', data, api.getExternalTransferPay );
}

export function getExternalTransferSuccess(data) {
    return ajax.postAfterLoginQuery( '012025', data, api.getExternalTransferSuccess );
}



