<template>
	<div class="large-deposit-buy-success">
		<mt-cell title="提示信息"></mt-cell>
		<div class="success-contain">
			<mt-cell title="交易成功！"></mt-cell>
			<mt-cell title="大额存单账户：" :value="detail.account"></mt-cell>
			<mt-cell title="存单本金：" :value="detail.money"></mt-cell>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default {
	data() {
		return {
			detail: {
				account: '',
				money: ''
			}
		};
	},
	methods: {
		back() {
			this.$router.push({name: 'LargeDepositBuy'});
		},
		submitBuyProduct(account) {
			service.submitBuyProduct({
				account: account
			}).then(data => {
				this.detail = data.data.data;
			});
		}
	},
	created() {
		this.submitBuyProduct(this.$route.params.account);
	}
};

</script>

<style lang="scss" scoped>

.large-deposit-buy-success{
	.mint-cell:after{
		height: 0;
	}
	.success-contain{
		border-top: 1px solid #d9d9d9;
		border-bottom: 1px solid #d9d9d9;
	}
}

</style>


