import ajax from '@/services/ajax';
import api from '@/services/api';

export function getLargeAmountOfRemittanceList(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getLargeAmountOfRemittanceList );
}

export function getLargeAmountOfRemittanceIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getLargeAmountOfRemittanceIndex );
}

export function getLargeAmountOfRemittancePay(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getLargeAmountOfRemittancePay );
}

export function getLargeAmountOfRemittanceSuccess(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getLargeAmountOfRemittanceSuccess );
}



