<template>
    <div>
        <div class="field-part">
            <div class="large-acount-info">
                <div class="large-payee">
                    <span>付款人</span>
                    <span>{{ dataForm.payer }}</span>
                </div>
                <div class="large-account">
                    <span>付款人账户</span>
                    <span>{{ dataForm.account }}</span>
                    <span>可用余额 <label>{{ dataForm.balance }}</label> 元</span>
                </div>
            </div>
        </div>
        <div class="field-part">
            <cell-transfer-money 
                title="转账金额" placeholder="请输入转账金额" 
                :totalMoney="dataForm.balance" twoValidate="show" v-model="dataForm.money">
            </cell-transfer-money>
        </div>
        <span class="pattern-span">实时到账</span>
        <div class="field-part pattern-span-next">
            <cell class="item" title="收款人" :value="dataForm.payeeName"></cell>
            <cell class="item" title="收款账户" :value="dataForm.payeeAccount"></cell>
            <cell class="item" title="开户行" :value="dataForm.payeeBankName"></cell>
        </div>
        <div class="field-part">
            <mt-field label="附言" placeholder="选填" v-model="dataForm.remark" :maxlength="3"></mt-field>
        </div>
        <span class="field-remark-dest">转账附言70字以内</span>
        <div class="field-action">
            <submit-button @submit="search()" :submitStatus="submitStatus" size="large" value="下一步" type="submit"></submit-button>
        </div>
    </div>
	
</template>

<script>

export default {
    data() {
        return {
            submitStatus: false,
            dataForm: {}
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.largeAmountOfRemittance.search);
        this.dataForm.money = '';
        this.dataForm.remark = '';
    },
    mounted(){
    },
    methods: {
        search () {
            this.$router.push("/large-amount-of-remittance/pay");
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.money && item.payeeName && item.payeeAccount && item.payeeBankName){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateLargeAmountOfRemittanceSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearLargeAmountOfRemittanceSearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.large-acount-info {
    width: 750px;
    height: 278px;
    background-image: url('../../../../assets/img/bank/transfer/large_account_bg@2x.png');
    background-image: image-set(url('../../../../assets/img/bank/transfer/large_account_bg@3x.png') 3x);
    background-repeat: no-repeat;
    background-size: 100%;
    .large-payee {
        span {
            display: inline-block;
            padding: 28px 0px 0px 50px;
            font-size: 15px;
            line-height: 42px;
            color: #333333;
        }
        span:nth-child(2) {
            float: right;
            margin-right: 50px;
        }
    }
    .large-account {
        width: 750px;
        position: absolute;
        top: 0;
        span {
            display: inline-block;
            padding: 142px 0px 0px 50px;
            font-size: 16px;
            line-height: 44px;
            font-size: 16px;
            color: #272727;
        }
        span:nth-child(2) {
            float: right;
            margin-right: 50px;
            font-family: Farrington7B_Qiqi, "Helvetica Neue", Helvetica, STHeitiTC, sans-serif;
            letter-spacing: 2.29px;
        }
        span:nth-child(3) {
            float: right;
            margin-right: 50px;
            padding-top: 10px;
            font-size: 13px;
            color: #272727;
            label {
                color: #FF511A;
            }
        }
    }
}
.pattern-span {
    position: absolute;
    padding-top: 20px;
    right: 24px;
    font-family: PingFangSC-Regular;
    font-size: 13px;
    color: #666666;
    line-height: 24px;
    label {
        color: #26a2ff;
    }
}
</style>



