<template>
	<div class="large-deposit-search-tsearch-detail">
		<div class="products">
			<a v-for="item in list" class="product-item" href="javascript:;">
				<mt-cell title="序号1"></mt-cell>
				<mt-cell title="大额存单账户：" :value="item.type"></mt-cell>
				<mt-cell title="交易类型：" :value="item.account"></mt-cell>
				<mt-cell title="交易时间：" :value="item.money"></mt-cell>
				<mt-cell title="交易金额：" :value="item.date"></mt-cell>
				<mt-cell title="对方卡号：" :value="item.rate"></mt-cell>
				<mt-cell title="对方户名：" :value="item.date1"></mt-cell>
			</a>
		</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default{
	data() {
		return {
			list: []
		};
	},
	methods: {
		back() {
			this.$router.push({name: 'LargeDepositSearchTransaction'});
		},
		getLargeTransactionList(account) {
			service.getLargeTransactionList({
				account: account
			}).then(data => {
				this.list = data.data.data;
			});
		}
	},
	created() {
		this.getLargeTransactionList(this.$route.params.account);
	}
};

</script>

<style lang="scss">
	
.large-deposit-search-tsearch-detail{
	.product-title{
		border-bottom: 1px solid #d9d9d9;
	}
	.product-item{
		display: block;
		border-bottom: 1px solid #d9d9d9;
	}
	.mint-field-other{
		top:50%;
	}
	.mint-cell-title{
		width: 4rem;
	}
	.mint-cell:after{
		height: 0;
	}
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}

</style>


