<template>
    <div>
        <div class="bank-account">
            <div class="bank-account-items">
            <!-- <i :class=" 'icon icon-menu-201'"></i> -->
                <a href="#/my-account-transfer/index">
                    <div class="items-img icon-menu-211"></div>
                    <span>本人账户转账</span>
                </a>
            </div>
            <div class="bank-account-items">
                <a href="#/internal-transfer/index">
                    <div class="items-img icon-menu-212"></div>
                    <span>行内转账</span>
                </a>
            </div>
            <div class="bank-account-items">
                <a href="#/external-transfer/index">
                    <div class="items-img icon-menu-213"></div>
                    <span>行外转账</span>
                </a>
            </div>
            <div class="bank-account-items">
                <a href="#/agricultural-credit-transfer/index">
                    <div class="items-img icon-menu-214"></div>
                    <span>农信银转账</span>
                </a>
            </div>
            <div class="bank-account-items">
                <a href="#/large-amount-of-remittance/list">
                    <div class="items-img icon-menu-215"></div>
                    <span>定向大额转账</span>
                </a>
            </div>
            <div class="bank-account-items">
                <a href="#/device-revocation/index">
                    <div class="items-img icon-menu-217"></div>
                    <span>自助设备转账撤销</span>
                </a>
            </div>
        </div>
        <div class="recent-transfer">
            <span class="recent-span">最近转账：</span>
            <div class="recent-transfer-none" v-if=" recentTransferList.length < 1 ">
                <span>发起一笔行内、行外、农信银转账，</span>
                <span>这些转账的最近收款人将会收录在这里哦～</span>
            </div>
            <transfer-card v-for="item in recentTransferList" :key="item.id" @transfer-card-click="toTransfer(item)"
                :payee="item.payee" :payeeAccount="item.payeeAccount" :payeeBank="item.payeeBank">
            </transfer-card>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/bank/accounts';

export default {
    data() {
        return {
            recentTransferList: []
        };
    },
    created() {
        console.log( this.$route.meta.menuAuth );
        service.getAccountsIndex().then((res) => {
            this.recentTransferList = res.data.data;
        }, (err) => {
            console.log( err );
        });
    },
    computed: {
    },
    components: {
    },
    methods: {
        toTransfer (item){
            console.log(item.id);
        }
    }
};
</script>


<style lang="scss">
.bank-account {
    font-size: 0;
    -webkit-text-size-adjust:none;
    padding-top: 40px;
    .bank-account-items {
        text-align: center;
        display: inline-block;
        margin: 0px 0px 22px 30px;
        height: 168px;
        width: 330px;
        background: #FFFFFF;
        border-radius: 10px;
        .items-img {
            height: 60px;
            width: 60px;
            margin: 30px auto 14px auto;
            position: initial;
            top: initial;
            left: initial;
            transform: initial;
        }
        span {
            font-size: 16px;
        }
    }
}
.recent-transfer {
    position: absolute;
    overflow: hidden;
    background: #fff;
    width: 100%;
    min-height: 600px;
    height: auto;
    margin-top: 16px;
    .recent-span {
        padding: 14px 0px 20px 30px;
        display: inline-block;
        font-size: 14px;
        color: #2A3342;
        // border-left: 0.5px solid rgba(181, 181, 181, 0.41);
    }
    .recent-transfer-none {
        text-align: center;
        padding-top: 200px;
        span {
            display: block;
        }
    }
    .transfer-card:last-child {
        border-bottom: 0px;
    }
}
</style>



