<template>
	<div class="large-deposit-replace">
        <div class="cell-part">
            <mt-cell @click.native="showSelectorPaymentAccount()" title="卡号：" placeholder="" :value="PAYEE_ACCOUNT" is-link>
            </mt-cell>
        </div>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<div class="button-bottom">
			<mt-button @click.native="back" type="primary">返回</mt-button>
            <mt-button @click.native="submit" type="primary">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import * as bservice from '@/services/public';

export default{
	data() {
		return {
			PAYEE_ACCOUNT: '',
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: []
            }
		};
	},
	created() {
		this.account = this.account || this.$store.state.largeDepositReplace.account;
        this.getCommonAccountList();
    },
    methods: {
        getCommonAccountList(type) {
            bservice.getCommonAccountList({
                flagType: '0'
            }).then( data => {
                this.selectorPaymentAccount.accountList = data.LIST;
                if(type){
                    this.paymentAccount();
                }
            }, err => {
                console.log( err );
            });
        },
        showSelectorPaymentAccount() {
            if(this.selectorPaymentAccount.accountList.length == 0){
                this.getCommonAccountList(true);
            } else{
                this.paymentAccount();
            }
        },
        paymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.PAYEE_ACCOUNT = data.item.ACCOUNT;
            }, data => {
            });
        },
        submit() {
            this.$router.push({name: 'LargeDepositReplaceReplace', params: {PAYEE_ACCOUNT: this.PAYEE_ACCOUNT}});
        },
        back() {
            history.back();
        }
    },
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateLargeDepositReplace', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateLargeDepositReplace', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.large-deposit-replace{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


