<template>
	<div>
        <div class="field-part">
            <account-select title="卡号" :value=" dataForm.PAYMENT_ACCOUNT || '请选择卡号' " 
                :balance="selectorPaymentAccount.checked.BALANCE"
                type="complex" @click.native=" showSelectorPaymentAccount() ">
            </account-select>
        </div>
        <div class="field-part">
            <cell-transfer-money 
                title="转账金额" placeholder="请输入转账金额" 
                :totalMoney="selectorPaymentAccount.checked.BALANCE" twoValidate="show" v-model="dataForm.money">
            </cell-transfer-money>
            <span class="pattern-span">实时到账</span>
        </div>
        <div class="field-part pattern-span-next">
            <mt-cell label="收款人" :value="dataForm.PAYEE_NAME"></mt-cell>
            <mt-cell label="收款账户" :value="dataForm.PAYEE_ACCOUNT"></mt-cell>
        </div>
        <div class="field-part">
            <mt-field label="附言：" placeholder="选填" v-model="dataForm.REMARK"></mt-field>
        </div>
        <span class="field-remark-dest">转账附言70字以内</span>
        <div class="field-action">
            <submit-button @submit="search()" :submitStatus="submitStatus" value="下一步" size="large" type="submit"></submit-button>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>   
    </div>
</template>

<script>

import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            submitStatus: false,
            selectorPaymentAccount: {
                title: '选择付款账号',
                type: 'payment',
                checked: {},
                showAddBtn: false,
                showWarn: true,
                showBankName: false,
                accountList: []
            },
            dataForm: {
                checked: {},
                PAYEE_NAME: '', // 收款户名
                PAYEE_ACCOUNT: '', // 收款账号
                PAYMENT_ACCOUNT: '', // 付款账户
                TRAN_CURRENCY: '', // 付款币种
                TRAN_AMOUNT: '', // 转账金额
                FEE: '', // 转账手续费
                REMARK: '' // 附言
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.twoDimensionCode.search);
        this.dataForm.PAYEE_NAME = 'payeeName';
        this.dataForm.PAYEE_ACCOUNT = '622222****1234';
        service.getAccountList().then((res) => {
            if(!this.dataForm.PAYMENT_ACCOUNT){
                let defaultAccount = res.data.filter(( item )=>{ return item.isDefault })[0] || {};
                this.selectorPaymentAccount.checked = defaultAccount;
                this.dataForm.PAYMENT_ACCOUNT = defaultAccount.ACCOUNT;
                this.dataForm.checked = defaultAccount;
            }else{
                this.selectorPaymentAccount.checked = this.dataForm.checked;
            }
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            var _error = [];
            if( this.$validate.isEmpty( _data.PAYMENT_ACCOUNT ) ) {
                _error.push( '银行卡号不能为空。' );
            }
            if( _error.length > 0 ) {
                this.$hybrid.call({
                    method: 'alert',
                    params: {
                        title: '',
                        message: _error[0],
                        confirmButtonText: '确定'
                    }
                });
                return;
            }
    		var params = {
    			path: '/two-dimension-code/payment/pay',
    			query: {
    				id: _data.account
    			}
    		};
            this.$router.push(params);
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.PAYMENT_ACCOUNT = data.item.ACCOUNT;
                this.dataForm.checked = data.item;
            }, ( data ) => {});
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.account){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] ){
            this.$store.dispatch('updateTwoDimensionCodeSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearTwoDimensionCodeSearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">

</style>



