<template>
	<div class="large-deposit-transfer-out-transfer">
		<mt-cell title="大额存单账户：" :value="detail.account"></mt-cell>
		<mt-cell title="存单本金：" :value="detail.account"></mt-cell>
		<mt-cell title="提取支取本息合计：" :value="detail.account"></mt-cell>
		<mt-cell title="到期本息合计：" :value="detail.account"></mt-cell>
		<mt-cell title="出售金额：" :value="detail.account"></mt-cell>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="page-part">
      		<mt-field class="code" label="验证码：" placeholder="输入验证码" :attr="{ maxlength: 6 }">
        		<mt-button type="primary">获取验证码</mt-button>
      		</mt-field>
    	</div>
    	<div class="field-warn">
    		提示：验证码为6位数字
    	</div>
		<div class="field-action">
            <submit-button @submit="submit" :submitStatus="submitStatus" value="获取转让码（有效期为当天）"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default{
	data() {
		return {
            detail: {
    			account: '',
                account2: ''
            },
			code: '',
            submitStatus: false,
            cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
    watch: {
        'detail.account': function(val) {
            // if(val){
                this.submitStatus = true;
            // }else{
            //     this.submitStatus = false;
            // }
        }
    },
	methods: {
        submit() {
        	this.submitTransferProduct();
        },
        back() {
        	history.back();
        },
        getSureOutProductDetail() {
            service.getSureOutProductDetail()
            .then(data => {
                this.detail = data.data.data;
            });
        },
        submitTransferProduct() {
            service.submitTransferProduct()
            .then(data => {
                this.$router.push({name: 'LargeDepositTransferOutSuccess'});
            });
        }
	},
	created() {
		this.getSureOutProductDetail();
	}
};

</script>

<style lang="scss">
.large-deposit-transfer-out-transfer{
	.code{
		.mint-field-other{
			button{
				height: 70%;
				margin-top: 6%;
				margin-right: 16px;
			}
		}
	}
}
</style>


