<template>
	<div class="large-deposit-buy">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="卡号" placeholder="" :value="detail.ACCOUNT" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
        <mt-cell title="产品状态" @click.native=" showSelectorStatus()  "is-link :value=" detail.STATUS || '请选择' ">
        </mt-cell>
        <mt-cell title="产品期限" @click.native=" showSelectorPeriod()  "is-link :value=" detail.PERIOD || '请选择' ">
        </mt-cell>
        <mt-cell title="排序方式" @click.native=" showSelectorOrder()  "is-link :value=" detail.ORDER_TYPE || '请选择' ">
        </mt-cell>
		<div class="field-action">
            <mt-button @click.native="back" type="primary">返回</mt-button>
            <mt-button @click.native="submit" type="primary">确定</mt-button>
		</div>
        <selector-action ref="selectorStatus" :options=" selectorStatus "></selector-action>
        <selector-action ref="selectorPeriod" :options=" selectorPeriod "></selector-action>
        <selector-action ref="selectorOrder" :options=" selectorOrder "></selector-action>
	</div>
</template>

<script>

import * as bservice from '@/services/public';

export default{
	data() {
		return {
            detail: {
                ACCOUNT: '',
                STATUS: '可购买',
                STATUS_ID: '',
                PERIOD: '不限',
                PERIOD_ID: '',
                ORDER_TYPE: '发行日期',
                ORDER_TYPE_ID: ''
            },
            selectorStatus: {
                title: '请选择产品状态',
                key: 'field1',
                value: 'field2',
                checked: 2,
                actionList: [
                    {
                        field1: '',
                        field2: '不限'
                    },
                    {
                        field1: 1,
                        field2: '即将发行'
                    },
                    {
                        field1: 2,
                        field2: '可购买'
                    },
                    {
                        field1: 3,
                        field2: '已发行（查三个月）'
                    }
                ]
            },
            selectorPeriod: {
                title: '请选择产品期限',
                key: 'field1',
                value: 'field2',
                checked: '',
                actionList: [
                    {
                        field1: '',
                        field2: '不限'
                    },
                    {
                        field1: 1,
                        field2: '1个月'
                    },
                    {
                        field1: 3,
                        field2: '3个月'
                    },
                    {
                        field1: 6,
                        field2: '6个月'
                    },
                    {
                        field1: 9,
                        field2: '9个月'
                    },
                    {
                        field1: 12,
                        field2: '12个月'
                    },
                    {
                        field1: 18,
                        field2: '18个月'
                    },
                    {
                        field1: 24,
                        field2: '24个月'
                    },
                    {
                        field1: 36,
                        field2: '36个月'
                    },
                    {
                        field1: 60,
                        field2: '60个月'
                    }
                ]
            },
            selectorOrder: {
                title: '请选择排序方式',
                key: 'field1',
                value: 'field2',
                checked: 1,
                actionList: [
                    {
                        field1: '',
                        field2: '存单利率'
                    },
                    {
                        field1: 1,
                        field2: '发行日期'
                    }
                ]
            },
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: []
            }
		};
	},
	created() {
		this.detail = Object.assign({}, this.$store.state.largeDepositBuy.detail);
        this.value = Object.assign({}, this.$store.state.largeDepositBuy.value);
        this.getCommonAccountList();
	},
    methods: {
        submit() {
            this.$router.push({name: 'LargeDepositBuyList', params: { detail: this.detail }});
        },
        back() {
            history.back();
        },
        getCommonAccountList(type) {
            bservice.getCommonAccountList({
                flagType: '0'
            }).then( data => {
                this.selectorPaymentAccount.accountList = data.LIST;
                if(type){
                    this.paymentAccount();
                }
            }, err => {
                console.log( err );
            });
        },
        showSelectorPaymentAccount() {
            if(this.selectorPaymentAccount.accountList.length == 0){
                this.getCommonAccountList(true);
            } else{
                this.paymentAccount();
            }
        },
        paymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.ACCOUNT = data.item.ACCOUNT;
            }, data => {
            });
        },
        showSelectorStatus() {
            this.$refs.selectorStatus.show().then((data) => {
                this.selectorStatus.checked = data.item.field1;
                this.detail.STATUS = data.item.field2;
                this.detail.STATUS_ID = data.item.field1;
            }, (data) => {
            });
        },
        showSelectorPeriod() {
            this.$refs.selectorPeriod.show().then((data) => {
                this.selectorPeriod.checked = data.item.field1;
                this.detail.PERIOD = data.item.field2;
                this.detail.PERIOD_ID = data.item.field1;
            }, (data) => {
            });
        },
        showSelectorOrder() {
            this.$refs.selectorOrder.show().then((data) => {
                this.selectorOrder.checked = data.item.field1;
                this.detail.ORDER_TYPE = data.item.field2;
                this.detail.ORDER_TYPE_ID = data.item.field1;
            }, (data) => {
            });
        }
    },
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateLargeDepositDeail', this.detail);
            this.$store.dispatch('updateLargeDepositValue', this.value);
        }else{
            this.$store.dispatch('clearLargeDepositDetail');
            this.$store.dispatch('clearLargeDepositValue');
        }
        next( true );
    }
};

</script>

<style lang="scss">
.large-deposit-buy{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


