<template>
	<div class="transaction-detail">
		<div class="field-part">
			<mt-cell title="卡号" is-link @click.native=" showSelectorPaymentAccount() " 
				:value="  dataForm.account || '请选择卡号' " ></mt-cell>
		</div>
		<div class="field-part">
			<cell-date-zone title="时间跨度" zone="1W, 1M, 3M"  ref="dateZone" @date-zone-change=" dateZoneChange " ></cell-date-zone>
			<mt-cell title="起始日期" :value=" dataForm.beginDate " class="cell-text-info">
			</mt-cell>
			<mt-cell title="结束日期" :value=" dataForm.endDate " class="cell-text-info">
			</mt-cell>
		</div>
		<div class="cell-hint">
			仅支持查询时间跨度三个月内的交易明细记录
		</div>

		<div class="field-action">
			<mt-button @click.native="search()" size="large" type="primary">查询</mt-button>
		</div>

		<selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
	</div>
</template>

<script>

import * as bservice from '@/services/public';

export default {
    data() {
        return {
            dataForm: {
                account: '',
		        beginDate: '',
		        endDate: ''
            },
			selectorPaymentAccount: {
                title: '选择账户',
                type: 'payment',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                showBankName: false,
                accountList: []
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.transactionDetail.search);
        this.getCommonAccountList();
    },
    mounted(){
        this.$refs.dateZone.setDefault( this.dataForm.beginDate, this.dataForm.endDate );
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            this.$router.push({name: 'TransactionDetailList'});
        },
        getCommonAccountList(type) {
            bservice.getCommonAccountList({
                flagType: '0'
            }).then( data => {
                this.selectorPaymentAccount.accountList = data.LIST;
                let defaultAccount = '';
                if(this.dataForm.account){
                    defaultAccount = data.LIST.filter(( item )=>{ return item.ACCOUNT == this.dataForm.account })[0] || {};
                } else{
                    defaultAccount = data.LIST.filter(( item )=>{ return item.IS_DEFAULT })[0] || {};
                }
                this.selectorPaymentAccount.checked = defaultAccount;
                this.dataForm.account = defaultAccount.ACCOUNT;
                if(type){
                    this.paymentAccount();
                }
            }, err => {
                console.log( err );
            });
        },
        showSelectorPaymentAccount() {
            if(this.selectorPaymentAccount.accountList.length == 0){
                this.getCommonAccountList(true);
            } else{
                this.paymentAccount();
            }
        },
        paymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.account = data.item.ACCOUNT;
            }, ( data ) => {});
        },
        dateZoneChange ( begin, end ) {
			this.dataForm.beginDate = begin;
			this.dataForm.endDate = end;
		}

    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            this.$store.dispatch('updateTransactionDetailSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearTransactionDetailSearch');
        }
        next( true );
    }
};
</script>


