import ajax from '@/services/ajax';
import api from '@/services/api';

export function getInternalTransferFee(data) {
    return ajax.postAfterLoginQuery( '021001', data, api.getInternalTransferFee );
}

export function getInternalTransferPay(data) {
    return ajax.postAfterLoginUpdate( '021001', data, api.getInternalTransferPay );
}

export function getInternalTransferSuccess(data) {
    return ajax.postAfterLoginQuery( '012025', data, api.getInternalTransferPay );
}



