<template>
    <div class="device-revocation-row">
        <transferSuccessPay 
            :payee='dataForm.COR_NAME' :payeeAccount='dataForm.PAYEE_ACCOUNT' payeeBank='安徽农金' 
            :payeeMoney='dataForm.AMOUNT' :remark='dataForm.SENDMENO' transferType='1'></transferSuccessPay>
        <div class="detail-items">
            <cell-password :options=" cellPassword " ></cell-password>
        </div>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        <div class="field-action">
            <mt-button @click="showDialogSmsVerify()" size="large" type="primary">确认</mt-button>
        </div>
    </div>
</template>

<script>
import * as service from '@/services/bank/internal-transfer';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定转账',
                btnGetText: '获取验证码'
            },
            dataForm: {}
        };
    },
    created() {
        this.dataForm = Object.assign({}, this.$store.state.internalTransfer.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
                this.next();
            }, (data) => {
            });
        },
        next() {
            service.getInternalTransferPay({
                'TRAN_TYPE': this.dataForm.TRAN_TYPE,
                'COR_NAME': this.dataForm.COR_NAME,
                'PAYEE_ACCOUNT': this.dataForm.PAYEE_ACCOUNT,
                'PAYER_ACCOUNT': this.dataForm.PAYER_ACCOUNT,
                'CURRENCY': this.dataForm.CURRENCY,
                'AMOUNT': this.dataForm.AMOUNT,
                'FEE': this.dataForm.FEE,
                'SENDMENO': this.dataForm.SENDMENO,
                'ISMOBILETRAN': this.dataForm.ISMOBILETRAN,
                'PAY_PWD': '123456',
                'MSGCODE': '123456'
            }).then((res) => {
                if( res.RESPCODE == '0') {
                    this.success();
                }else{
                    this.$alert(res.MESSAGE, {
                        btn: { text: '完成' }
                    });
                }
            }, (err) => {
                console.log( err );
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        },
        success (){
            service.getInternalTransferSuccess().then((res) => {
                this.$router.push('/internal-transfer/success');
            }, (err) => {
                console.log( err );
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateInternalTransferSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearInternalTransferSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    .div-success-left {
        height: 90px;
        line-height: 90px;
        text-align: left;
        font-size: 30px;
        background: #fff;
        border: 1px solid #ddd;
        color: #ffc107;
    }
    .device-revocation-detail {
        background: #fff;
        span {
            display: block;
            padding: 5px 0 5px 15px;
        }
        
    }
}
</style>



