<template>
	<div class="large-deposit-transfer-in-transfer">
		<mt-cell title="付款账户：" :value="detail.account"></mt-cell>
		<mt-cell title="付款人名称：" :value="detail.account"></mt-cell>
		<mt-cell title="账户余额：" :value="detail.account"></mt-cell>
		<mt-cell title="手续费：" :value="detail.account"></mt-cell>
		<mt-cell title="收款人账号：" :value="detail.account"></mt-cell>
		<mt-cell title="收款人名称：" :value="detail.account"></mt-cell>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="page-part">
      		<mt-field class="code" label="验证码：" placeholder="输入验证码" :attr="{ maxlength: 6 }">
        		<mt-button type="primary">获取验证码</mt-button>
      		</mt-field>
    	</div>
    	<div class="field-warn">
    		提示：验证码为6位数字
    	</div>
		<div class="field-action">
            <submit-button @submit="submit" :submitStatus="submitStatus" value="提交"></submit-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default{
	data() {
		return {
			detail: {
				account: '',
				account2: ''
			},
			code: '',
            submitStatus: false,
            cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
    watch: {
        'detail.account': function(val) {
            // if(val){
                this.submitStatus = true;
            // }else{
            //     this.submitStatus = false;
            // }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.submitInTransferProduct();
        },
        back() {
        	history.back();
        },
        getSureInProductDetail() {
        	service.getSureInProductDetail()
        	.then(data => {
        		this.detail = data.data.data;
        	});
        },
        submitInTransferProduct() {
        	service.submitInTransferProduct()
        	.then(data => {
        		this.$router.push({name: 'LargeDepositTransferInSuccess'});
        	});
        }
	},
	created() {
		this.getSureInProductDetail();
	}
};

</script>

<style lang="scss">
.large-deposit-transfer-in-transfer{
	.code{
		.mint-field-other{
			button{
				height: 70%;
				margin-top: 6%;
				margin-right: 16px;
			}
		}
	}
}
</style>


