<template>
	<div class="large-deposit-transfer-in-index">
		<mt-cell title="卡号：" :value="detail.account"></mt-cell>
		<mt-field label="转让码：" placeholder="请输入转让码"></mt-field>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">确定</mt-button>
		</div>
	</div>
</template>

<script>

export default {
	data() {
		return {
			detail: {
				account: ''
			}
		};
	},
	methods: {
		submit() {
			this.$router.push({name: 'LargeDepositTransferInDetail'});
		},
		back() {
			history.back();
		}
	},
	created() {
		this.detail.account = this.$route.params.account;
	}
};

</script>


