<template>
    <div style="background: #fff">
        <tip-page tipTitle="转账成功" :tipMsg="accountsDetail.patternName"></tip-page>
        <transfer-success-detail 
            payer='test' :payerAccount='accountsDetail.PAYER_ACCOUNT' payerBank='安徽农金' 
            :payee='accountsDetail.COR_NAME' :payeeAccount='accountsDetail.PAYEE_ACCOUNT' payeeBank='安徽农金' 
            :payeeMoney='accountsDetail.AMOUNT' :remark='accountsDetail.SENDMENO' transferType='1'></transfer-success-detail>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toIndex()">返回首页</mt-button>
        </div>
        <div class="field-action inline">
            <mt-button size="large" @click = "toAccountIndex()">继续转账</mt-button>
            <mt-button size="large" @click = "toInternalTransferIndex()">返回转账首页</mt-button>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            accountsDetail: []
        };
    },
    created() {
        this.accountsDetail = Object.assign({}, this.$store.state.internalTransfer.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        toIndex() {
            this.$router.push('/');
        },
        toAccountIndex() {
            this.$router.push('/internal-transfer/index');
        },
        toInternalTransferIndex() {
            this.$router.go(-3);
        }
    }
};
</script>


<style lang="scss">
</style>



