<template>
    <div>
        <ul class="card-query-detail" v-for="item in list" :key="item.id">
            <li class="card-item card-item-into" v-if=" item.transferType=='1'">
                <span class="type">入账</span>
                <span class="money">+ {{item.transferMoney}}</span>
            </li>
            <li class="card-item card-item-out" v-else>
                <span class="type">出账</span>
                <span class="money">- {{item.transferMoney}}</span>
            </li>
            <li class="card-item">
                <span class="date">{{item.transferDate}}</span>
                <span class="moneyType">{{item.moneyType}}</span>
            </li>
            <li class="card-item">
                <span class="dest">{{item.merchant}}</span>
                <span class="smoney">余额：{{item.ymoney}} 元</span>
            </li>
        </ul>
    </div>
</template>

<script>

import * as service from '@/services/bank/fund-transfer';

export default {
    data() {
        return {
            list: []
        };
    },
    created() {
        this.getList();
    },
    computed: {
    },
    components: {
    },
    methods: {
        getList (){
            service.getFundTransferCardQueryPayDetail().then((res) => {
                this.list = res.data.data;
            }, (err) => {
                console.log( err );
            });
        }
    }
};
</script>


<style lang="scss">
.card-query-detail {
    padding: 20px 36px 20px 86px;
    background: #fff;
    min-height: 166px;
    border: 0.5px solid rgba(181,181,181,0.41);
    li {
        display: flex;
    }
    span {
        float: left;
        width: 50%;
        font-size: 13px;
        color: #B5B5B5;
    }
    span:nth-child(even) {
        text-align: end;
    }
    .card-item {
        .type {
            font-size: 16px;
            color: #272727;
            line-height: 44px;
        }
        .date {
            line-height: 36px;
            padding-top: 6px;
        }
        .moneyType {
            color: #979797;
            line-height: 36px;
            padding-top: 6px;
        }
        .dest {
            line-height: 36px;
            padding-top: 4px;
            color: #666666;
        }
        .smoney {
            line-height: 36px;
            padding-top: 4px;
        }
    }
    .card-item-into {
        .money {
            font-size: 16px;
            color: #FF942F;
        }
    }
    .card-item-out {
        .money {
            font-size: 16px;
            color: #46C300;
        }
    }
    .card-item-into:after {
        content: '';
        position: absolute;
        width: 16px;
        height: 16px;
        background-color: #46C300;
        border-radius: 50%;
        left: 50px;
        margin-top: 15px;
    }
    .card-item-out:after {
        content: '';
        position: absolute;
        width: 16px;
        height: 16px;
        background-color: #FF942F;
        border-radius: 50%;
        left: 50px;
        margin-top: 15px;
    }
}
</style>



