import ajax from '@/services/ajax';
import api from '@/services/api';

export function getFundTransferIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getFundTransferIndex );
}

export function getFundTransferCardQueryIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getFundTransferCardQueryIndex );
}

export function getFundTransferCardQueryInfo(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getFundTransferCardQueryInfo );
}

export function getFundTransferCardQueryPayIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getFundTransferCardQueryPayIndex );
}

export function getFundTransferCardQueryPayDetail(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getFundTransferCardQueryPayDetail );
}
export function getFundTransferCardIntoIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getFundTransferCardIntoIndex );
}
export function getFundTransferCardIntoPay(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getFundTransferCardIntoPay );
}
export function getFundTransferCardIntoSuccess(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getFundTransferCardIntoSuccess );
}


