import ajax from '@/services/ajax';
import api from '@/services/api';

export function getTwoDimensionCodeRefresh(data) {
    return ajax.postAfterLoginQuery( '021008', data, api.getTwoDimensionCodeRefresh );
}

export function getTwoDimensionCodeIndex(data) {
    return ajax.postAfterLoginQuery( '021006', data, api.getTwoDimensionCodeIndex );
}

export function getTwoDimensionCodePay(data) {
    return ajax.postAfterLoginQuery( '021006', data, api.getTwoDimensionCodePay );
}

export function getTwoDimensionCodeSuccess(data) {
    return ajax.postAfterLoginQuery( '021006', data, api.getTwoDimensionCodeSuccess );
}


