<template>
	<div class="large-deposit-search-csearch-list">
		<a @click="detail()" v-for="item in list" href="javascript:;" class="buy-list-item">
			<div class="item-title">大额存单账户{{item.account}}</div>
			<div class="item-contain">
				<div class="item-label">
					<label>产品名称</label>
					<p>{{item.account}}</p>
				</div>
				<div class="item-label">
					<label>存单本金</label>
					<p>{{item.account}}</p>
				</div>
				<div class="item-label">
					<label>期限</label>
					<p>{{item.account}}</p>
				</div>
			</div>
			<div class="item-contain">
				<div class="item-label">
					<label>存单利率</label>
					<p>{{item.account}}</p>
				</div>
				<div class="item-label">
					<label>到期日</label>
					<p>{{item.account}}</p>
				</div>
				<div class="item-label">
					<label>账户状态</label>
					<p>{{item.account}}</p>
				</div>
			</div>
		</a>
		<div class="field-action">
            <mt-button @click.native="back" type="primary">返回</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default{
	data() {
		return {
			list: []
		};
	},
	methods: {
		detail(id){
            this.$router.push({name: 'LargeDepositSearchCustomDetail'});
		},
		back() {
        	history.back();
        },
        getCsearchList() {
        	service.getCsearchList()
        	.then(data => {
        		this.list = data.data.data;
        	});
        }
	},
	created() {
		this.getCsearchList();
	}
};

</script>

<style lang="scss">

.large-deposit-search-csearch-list{
	.buy-list-item{
		display: block;
		background-color: #fff;
		padding: 16px;
		border-top: 1px solid #d9d9d9;
	}
	.buy-list-item + .buy-list-item{
		border-bottom: 1px solid #d9d9d9;
	}
	.buy-list-item:last-child{
		border-bottom: 1px solid #d9d9d9;
	}
	.item-title{
		font-size: 14px;
		margin-bottom: 16px;
	}
	.item-contain{
		margin-bottom: 16px;
	}
	.item-label{
		display: inline-block;
		width: 30%;
		text-align: center;
		label{
			font-size: 14px;
		}
	}
	.item-label:nth-child(2){
		margin-right: 2%;
		margin-left: 2%;
	}
}

</style>


