<template>
    <div>
        <div v-if="showList">
            <span class="amount-title">已签约收款账户</span>
            <transfer-card v-for="item in dataForm.list" :key="item.id" @transfer-card-click="toTransfer(item)"
                :payee="item.payee" :payeeAccount="item.payeeAccount" :payeeBank="item.payeeBank">
            </transfer-card>
            <span class="amount-dest" v-if="dataForm.list.length < 5">最多可设置<label class="amount-label">5</label>个收款账户</span>
            <span class="amount-dest" v-else>最多可设置<label class="amount-label">5</label>个收款账户，您已达上限</span>
        </div>
        <div v-else class="amount-list-none">
            <div class="notice-img"></div>
            <p>您尚未开通定向大额转账功能，请通过安徽农金网上银行专业版或携带本人身份证前往安徽农金营业网点进行设置</p>
        </div>
        
    </div>
    
</template>

<script>

import * as service from '@/services/bank/large-amount-of-remittance';

export default {
    data() {
        return {
            showList: false,
            dataForm: {
                list: [],
                payeeName: '',
                payeeAccount: '',
                payeeBankName: '',
                bankName: '',
                account: '',
                payer: '',
                money: '',
                balance: '',
                remark: ''
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.largeAmountOfRemittance.search);
        if(this.dataForm.list.length < 1) {
            service.getLargeAmountOfRemittanceList().then((res) => {
                this.dataForm.list = res.data.data;
                this.showList = true;
            }, (err) => {
                console.log( err );
            });
        }else{
            this.showList = true;
        }
        this.dataForm.payer = "test鸿";
        this.dataForm.account = "62222****1234";
        this.dataForm.balance = "90001";
    },
    mounted(){
    },
    methods: {
        toTransfer (item) {
            this.dataForm.payeeName = item.payee;
            this.dataForm.payeeAccount = item.payeeAccount;
            this.dataForm.payeeBankName = item.payeeBank;
            this.$router.push("/large-amount-of-remittance/index");
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            var d = this.dataForm;
            this.$store.dispatch('updateLargeAmountOfRemittanceSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearLargeAmountOfRemittanceSearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.amount-title {
    margin: 20px 0px 20px 50px;
    display: block;
    line-height: 36px;
    color: #666666;
}
.amount-dest {
    display: block;
    text-align: center;
    margin-top: 20px;
    line-height: 28px;
    color: #666666;
    font-family: PingFangSC-Regular;
    letter-spacing: 0;
    .amount-label {
        color: #000000;
        font-family: PingFangSC-Medium;
        line-height: 28px;
    }
}
.amount-list-none {
    background: #fff;
    text-align: center;
    height: 100%;
    .notice-img {
        display: inline-block;
        height: 120px;
        width: 120px;
        margin: 46px auto 40px auto;
        background-image: url('../../../../assets/img/tip/notice@2x.png');
        background-image: image-set(url('../../../../assets/img/tip/notice@3x.png') 3x);
        background-repeat: no-repeat;
        background-size: 100%;
    }
    p {
        display: inline-block;
        width: 650px;
    }
}
</style>



