<template>
    <div>
        <transfer-success-pay 
            :payee='detail.payeeName' :payeeAccount='detail.payeeAccount' :payeeBank='detail.payeeBankName' 
            :payeeMoney='detail.money' :remark='detail.remark' transferType='6'></transfer-success-pay>
        <div class="detail-items">
            <cell-password :options=" cellPassword " ></cell-password>
        </div>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        <div class="field-action">
            <mt-button @click="showDialogSmsVerify()" size="large" type="primary">确认</mt-button>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/bank/two-dimension-code';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定转账',
                btnGetText: '获取验证码'
            },
            detail: {}
        };
    },
    created() {
        this.detail = Object.assign({}, this.$store.state.twoDimensionCode.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
                this.next();
            }, (data) => {
            });
        },
        next(id) {
            this.$router.push('/two-dimension-code/payment/success');
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateTwoDimensionCodeSearch', this.detail );
        }else{
            this.$store.dispatch('clearTwoDimensionCodeSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">

</style>



