<template>
    <div>
        <fund-card v-for="item in dataForm.cardList" :key="item.id" 
            :name="item.name" :account="item.account" :bankName="item.bankName" @card-query-click="getSelect( item )">
        </fund-card>
    </div>
    
</template>

<script>

import * as service from '@/services/bank/fund-transfer';

export default {
    data() {
        return {
            dataForm: {
                cardList: []
            }
        };
    },
    created(){
        service.getFundTransferCardQueryIndex().then((res) => {
            this.dataForm.cardList = res.data.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        getSelect (item){
            this.$ls.setItem('queryfundCard', item);
            this.$router.push('/card-query/info');
        }
    },
    watch: {
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateCardQuerySearch', this.dataForm );
        }else{
            this.$store.dispatch('clearCardQuerySearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">

</style>



