<template>
    <div>
        <transfer-success-pay 
            :payer='dataForm.PAYMENT_ACCOUNT' :payerAccount='dataForm.PAYMENT_ACCOUNT' payerBank='安徽农金' 
            :payee='dataForm.PAYEE_NAME' :payeeAccount='dataForm.PAYEE_ACCOUNT' :payeeBank='dataForm.PAYEE_BANK_NO' 
            :payeeMoney='dataForm.TRAN_AMOUNT' :remark='dataForm.SENDMENO' :fee='dataForm.FEE' transferType='4'></transfer-success-pay>
        <div class="detail-items">
            <cell-password :options=" cellPassword " ></cell-password>
        </div>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        <div class="field-action">
            <mt-button @click="showDialogSmsVerify()" size="large" type="primary">确认</mt-button>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/bank/external-transfer';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定转账',
                btnGetText: '获取验证码'
            },
            dataForm: {}
        };
    },
    created() {
        this.dataForm = Object.assign({}, this.$store.state.externalTransfer.search);
        console.log(this.dataForm);
    },
    computed: {
    },
    components: {
    },
    methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
                this.next();
            }, (data) => {
            });
        },
        next() {
            service.getExternalTransferPay({
                'TRAN_TYPE': this.dataForm.TRAN_TYPE,
                'PAYEE_NAME': this.dataForm.PAYEE_NAME,
                'PAYEE_ACCOUNT': this.dataForm.PAYEE_ACCOUNT,
                'PAYEE_BANK_NO': this.dataForm.PAYEE_BANK_NO,
                'PAYMENT_ACCOUNT': this.dataForm.PAYMENT_ACCOUNT,
                'TRAN_CURRENCY': this.dataForm.TRAN_CURRENCY,
                'TRAN_AMOUNT': this.dataForm.TRAN_AMOUNT,
                'TRAN_FEE': this.dataForm.TRAN_FEE,
                'SENDMENO': this.dataForm.SENDMENO,
                'PAY_PWD': '123456'
            }).then((res) => {
                if( res.RESPCODE == '0') {
                    this.success();
                }else{
                    this.$alert(res.MESSAGE, {
                        btn: { text: '完成' }
                    });
                }
            }, (err) => {
                console.log( err );
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        },
        success (){
            service.getExternalTransferSuccess().then((res) => {
                this.$router.push('/external-transfer/success');
            }, (err) => {
                console.log( err );
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        }
    }
};
</script>


<style lang="scss">

</style>



