        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            dataForm: {
                beginDate: '',
                endDate: ''
            }
        };
    },
    created() {
        // this.detail = Object.assign({}, this.$store.state.agriculturalCreditTransfer.search);
    },
    mounted(){
        this.dataForm.beginDate = "2017/06/17";
        this.dataForm.endDate = "2017/06/17";
    },
    methods: {
        search () {
            this.$router.push("/card-query/payDetail");
        }
    }
};
</script>


<style lang="scss">
.card-info {
    height: 120px;
    background-image: url('../../../../assets/img/bank/fund-transfer/transfer-pay-detail@2x.png');
    background-image: image-set(url('../../../../assets/img/bank/fund-transfer/transfer-pay-detail@3x.png') 3x);
    background-repeat: no-repeat;
    background-size: 100%;
    span {
        display: block;
        padding-left: 50px;
        padding-top: 20px;
    }
    span:nth-child(2) {
        padding-top: 8px;
    }
}
.card-info-img {
    position: absolute;
    top: 0px;
    height: 120px;
    width: 100%;
    background-image: url('../../../../assets/img/bank/fund-transfer/logo-anhui@2x.png');
