<template>
    <div class="transaction-detail">
        <div class="field-part">
            <account-select title="卡号" :value=" dataForm.account || '请选择卡号' " 
                type="complex" @click.native=" showSelectorPaymentAccount() ">
            </account-select>
            <cell class="item" title="余额" :value="dataForm.balance"></cell>
            <div v-if="showButton">
                <button class="my-button" @click="select">查询</button>
            </div>
            <div v-if="showLoding" class="loding-img">
                <svg xmlns:svg="http://www.w3.org/2000/svg" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" version="1.0" width="64px" height="64px" viewBox="0 0 128 128" xml:space="preserve"><g><path d="M122.5 69.25H96.47a33.1 33.1 0 0 0 0-10.5h26.05a5.25 5.25 0 0 1 0 10.5z" fill="#757575"/><path d="M112.04 97.83L89.47 84.8a33.1 33.1 0 0 0 5.25-9.1l22.57 13.03a5.25 5.25 0 0 1-5.28 9.1z" fill="#d5d5d5"/><path d="M88.68 117.35L75.65 94.78a33.1 33.1 0 0 0 9.1-5.25l13.02 22.57a5.25 5.25 0 1 1-9.1 5.25z" fill="#d5d5d5"/><path d="M58.7 122.57V96.5a33.1 33.1 0 0 0 10.5 0v26.07a5.25 5.25 0 0 1-10.5 0z" fill="#d5d5d5"/><path d="M30.1 112.1l13.04-22.57a33.1 33.1 0 0 0 9.1 5.25L39.2 117.35a5.25 5.25 0 1 1-9.1-5.25z" fill="#d5d5d5"/><path d="M10.6 88.74L33.16 75.7a33.1 33.1 0 0 0 5.25 9.1L15.88 97.83a5.25 5.25 0 1 1-5.25-9.1z" fill="#d5d5d5"/><path d="M5.37 58.75h26.06a33.1 33.1 0 0 0 0 10.5H5.37a5.25 5.25 0 0 1 0-10.5z" fill="#c8c8c8"/><path d="M15.85 30.17L38.4 43.2a33.1 33.1 0 0 0-5.24 9.1L10.6 39.25a5.25 5.25 0 1 1 5.25-9.1z" fill="#bababa"/><path d="M39.2 10.65l13.03 22.57a33.1 33.1 0 0 0-9.1 5.25l-13-22.57a5.25 5.25 0 1 1 9.1-5.25z" fill="#acacac"/><path d="M69.2 5.43V31.5a33.1 33.1 0 0 0-10.5 0V5.42a5.25 5.25 0 1 1 10.5 0z" fill="#9e9e9e"/><path d="M97.77 15.9L84.75 38.47a33.1 33.1 0 0 0-9.1-5.25l13.03-22.57a5.25 5.25 0 1 1 9.1 5.25z" fill="#919191"/><path d="M117.3 39.26L94.7 52.3a33.1 33.1 0 0 0-5.25-9.1l22.57-13.03a5.25 5.25 0 0 1 5.25 9.1z" fill="#838383"/><animateTransform attributeName="transform" type="rotate" values="0 64 64;30 64 64;60 64 64;90 64 64;120 64 64;150 64 64;180 64 64;210 64 64;240 64 64;270 64 64;300 64 64;330 64 64" calcMode="discrete" dur="1080ms" repeatCount="indefinite"></animateTransform></g></svg>
            </div>
        </div>
        <div class="field-part">
            <cell-transfer-money 
                title="转账金额" placeholder="请输入转账金额" 
                :totalMoney="dataForm.balance" v-model="dataForm.money">
            </cell-transfer-money>
        </div>
        
        <div class="field-part">
            <cell class="item" title="收款账号" :value="dataForm.payeeAccount"></cell>
        </div>
        <div class="field-action">
            <submit-button @submit="search()" :submitStatus="submitStatus" value="下一步" size="large" type="submit"></submit-button>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
    </div>
    
</template>

<script>

import * as service from '@/services/bank/selector';
import * as fundService from '@/services/bank/fund-transfer';

export default {
    data() {
        return {
            showButton: true,
            showLoding: false,
            submitStatus: false,
            dataForm: {
                account: '',
                money: '',
                balance: '',
                payeeName: '',
                payeeAccount: '',
                payeeBankName: ''
            },
            selectorPaymentAccount: {
                title: '选择付款账号',
                type: 'payment',
                checked: {},
                showAddBtn: false,
                showWarn: true,
                showBankName: false,
                accountList: []
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.cardInto.search);
        this.dataForm = Object.assign(this.dataForm, this.$store.getters.transactionDemo);
        service.getAccountList().then((res) => {
            if(!this.dataForm.account){
                let defaultAccount = res.data.filter(( item )=>{ return item.isDefault })[0] || {};
                this.selectorPaymentAccount.checked = defaultAccount;
                this.dataForm.account = defaultAccount.ACCOUNT;
                 this.dataForm.payeeName = defaultAccount.name;
                this.dataForm.checked = defaultAccount;
            }else{
                this.selectorPaymentAccount.checked = this.dataForm.checked;
            }
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });

        this.dataForm.payeeAccount = '62222****1230';
    },
    mounted(){
    },
    methods: {
        select() {
            this.showButton = false;
            this.showLoding = true;
            var self = this;
            fundService.getFundTransferCardQueryInfo().then((res) => {
                setTimeout(function(){
                    self.showLoding = false;
                    self.dataForm.balance = res.data.money;
                }, 1000);
            }, (err) => {
                console.log( err );
            });
        },
        search () {
            this.$router.push("/card-into/pay");
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.account = data.item.ACCOUNT;
                this.dataForm.checked = data.item;
                self.dataForm.balance = '';
                this.showButton = true;
                this.showLoding = false;
            }, ( data ) => {});
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.money){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            var d = this.dataForm;
            this.$store.dispatch('updateCardIntoSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearCardIntoSearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.my-button {
    width: 120px;
    height: 48px;
    position: absolute;
    right: 50px;
    margin-top: -72px;
    background: #FFFFFF;
    font-size: 13px;
    color: #FF511A;
    border: 1px solid #FF511A;
    border-radius: 100px;
}
.loding-img {
    width: 64px;
    height: 64px;
    position: absolute;
    right: 50px;
    margin-top: -80px;
}
</style>



