<template>
    <div>
        <transfer-success-pay 
            :payer='detail.payeeName' :payerAccount='detail.account' payerBank='' 
            :payee='detail.payeeName' :payeeAccount='detail.payeeAccount' :payeeBank='detail.payeeBankName' 
            :payeeMoney='detail.money' transferType='7'></transfer-success-pay>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        <div class="field-action">
            <mt-button @click="showDialogSmsVerify()" size="large" type="primary">确认</mt-button>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/bank/fund-transfer';

export default {
    data() {
        return {
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定转账',
                btnGetText: '获取验证码'
            },
            detail: {}
        };
    },
    created() {
        this.detail = Object.assign({}, this.$store.state.cardInto.search);
        console.log(this.detail);
    },
    computed: {
    },
    components: {
    },
    methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
                this.next();
            }, (data) => {
            });
        },
        next(id) {
            service.getFundTransferCardIntoSuccess().then((res) => {
                this.$router.push('/card-into/success');
            }, (err) => {
                console.log( err );
            });
        }
    }
};
</script>


<style lang="scss">

</style>



