import ajax from '@/services/ajax';
import api from '@/services/api';

export default {

    /* 大额存单签约换卡 */
    replaceCard: data => {
        return ajax.postAfterLoginUpdate('013257', data, api.replaceCard);
    },
    /* 大额存单购买 */
    // 获取购买产品列表
    getBuyProductList: data => {
    	return ajax.postAfterLoginQueryList('013601', data, api.getBuyProductList);
    },
    // 获取购买产品详情
    getBuyProductDetail: data => {
    	return ajax.post(api.getBuyProductDetail, data);
    },
    // 获取购买确认信息
    getSureBuyProductDetail: data => {
    	return ajax.post(api.getSureBuyProductDetail, data);
    },
    // 购买产品
    submitBuyProduct: data => {
    	return ajax.post(api.submitBuyProduct, data);
    },
    /* 大额存单支取 */
    // 获取可支取产品列表
    getDrawProductList: data => {
        return ajax.post(api.getDrawProductList, data);
    },
    // 获取可支取产品详情
    getDrawProductDetail: data => {
        return ajax.post(api.getDrawProductDetail, data);
    },
    // 获取支取确认信息
    getSureDrawProductDetail: data => {
        return ajax.post(api.getSureDrawProductDetail, data);
    },
    // 支取产品
    submitDrawProduct: data => {
        return ajax.post(api.submitDrawProduct, data);
    },
    /* 大额存单转让 */
    // 获取可转让产品列表
    getOutProductList: data => {
        return ajax.post(api.getOutProductList, data);
    },
    // 获取可转让产品详情
    getOutProductDetail: data => {
        return ajax.post(api.getOutProductDetail, data);
    },
    // 获取转让确认信息
    getSureOutProductDetail: data => {
        return ajax.post(api.getSureOutProductDetail, data);
    },
    // 转让产品
    submitTransferProduct: data => {
        return ajax.post(api.submitTransferProduct, data);
    },
    // 获取受让产品详情
    getInProductDetail: data => {
        return ajax.post(api.getInProductDetail, data);
    },
    // 获取受让确认信息
    getSureInProductDetail: data => {
        return ajax.post(api.getSureInProductDetail, data);
    },
    // 受让产品
    submitInTransferProduct: data => {
        return ajax.post(api.submitInTransferProduct, data);
    },
    /* 大额存单查询 */
    // 获取客户持有信息产品列表
    getCsearchList: data => {
        return ajax.post(api.getCsearchList, data);
    },
    // 获取客户持有信息产品详情
    getCsearchDetail: data => {
        return ajax.post(api.getCsearchDetail, data);
    },
    // 获取大额存单交易明细
    getLargeTransactionList: data => {
        return ajax.post(api.getLargeTransactionList, data);
    }
};



