<template>
	<div class="large-deposit-replace-replace">
		<mt-cell title="卡号：" :value="PAYEE_ACCOUNT"></mt-cell>
        <mt-cell @click.native="showSelectorPaymentAccount()" title="新卡号：" placeholder="" :value="PAYER_ACCOUNT" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<cell-password :options="cellPassword" ></cell-password>
		<div class="page-part">
      		<mt-field v-model="MSGCODE" class="code" label="验证码：" placeholder="输入验证码" :attr="{ maxlength: 6 }" v-number>
        		<mt-button type="primary">获取验证码</mt-button>
      		</mt-field>
    	</div>
    	<div class="field-warn">
    		提示：验证码为6位数字
    	</div>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
            <mt-button @click.native="submit" type="primary">提交</mt-button>
		</div>
	</div>
</template>

<script>

import * as bservice from '@/services/public';
import service from '@/services/bank/large-deposit';

export default{
	data() {
		return {
			PAYEE_ACCOUNT: '',
			PAYER_ACCOUNT: '',
			MSGCODE: '',
			selectorPaymentAccount: {
                title: '选择理财账户',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: []
            },
            cellPassword: {
                title: '交易密码：',
                rule: 'number'
            }
		};
	},
	created() {
		this.PAYEE_ACCOUNT = this.$route.params.PAYEE_ACCOUNT;
        this.getCommonAccountList();
	},
    methods: {
        getCommonAccountList(type) {
            bservice.getCommonAccountList({
                flagType: '0'
            }).then( data => {
                let list = data.LIST;
                for(let i = 0; i < list.length; i++){
                    if(list[i].ACCOUNT === this.PAYEE_ACCOUNT){
                        list.splice(i, 1);
                    }
                }
                this.selectorPaymentAccount.accountList = list;
                if(type){
                    this.paymentAccount();
                }
            }, err => {
                console.log( err );
            });
        },
        showSelectorPaymentAccount() {
            if(this.selectorPaymentAccount.accountList.length == 0){
                this.getCommonAccountList(true);
            } else{
                this.paymentAccount();
            }
        },
        paymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.PAYER_ACCOUNT = data.item.ACCOUNT;
            }, data => {
            });
        },
        submit() {
            this.replaceCard();
        },
        back() {
            history.back();
        },
        replaceCard() {
            service.replaceCard({
                PAYEE_ACCOUNT: this.PAYEE_ACCOUNT,
                PAYER_ACCOUNT: this.PAYER_ACCOUNT,
                PAY_PWD: '',
                MSGCODE: this.MSGCODE
            })
            .then(data => {
                this.$router.push({name: 'LargeDeposit'});
            }, err => {
                console.log(err);
            });
        }
    }
};

</script>

<style lang="scss">
.large-deposit-replace-replace{
	.code{
		.mint-field-other{
			button{
				height: 70%;
				margin-top: 6%;
				margin-right: 16px;
			}
		}
	}
}
</style>


