<template>
    <div>
        <mt-navbar v-model="selected">
          <mt-tab-item id="1">行内转账</mt-tab-item>
          <mt-tab-item id="2">二维码转账</mt-tab-item>
        </mt-navbar>
        <mt-tab-container v-model="selected">
          <mt-tab-container-item id="1">
            <div class="transaction-detail">
                <div class="field-part">
                    <div class="cell-part">
                        <account-select title="卡号" :value=" dataForm.PAYER_ACCOUNT || '请选择卡号' " 
                            :balance="selectorPaymentAccount.checked.BALANCE"
                            type="complex" @click.native=" showSelectorPaymentAccount() ">
                        </account-select>
                    </div>
                </div>
                <div class="field-part">
                    <cell-transfer-money 
                        title="转账金额" placeholder="请输入转账金额" 
                        :totalMoney="selectorPaymentAccount.checked.BALANCE" v-model="dataForm.AMOUNT">
                    </cell-transfer-money>
                </div>
                <div class="field-part field-group filter-payee">
                    <mt-field label="收款人" placeholder="收款人"  v-model=" dataForm.COR_NAME " disableClear>
                    </mt-field>
                    <mt-field label="收款账户" placeholder="收款账号" type="number" v-model=" dataForm.PAYEE_ACCOUNT " disableClear></mt-field>
                    <router-link to="/selector/payee" class="btn-selector btn-seletor-payee">
                        <i class="icon"></i>
                    </router-link>
                </div>
                <span class="pattern-span" @click="showSelectorTransferAging()">{{dataForm.patternName}}，<label>更换到账时间</label></span>
                <div class="field-part field-remark">
                    <mt-field label="附言" placeholder="选填" v-model="dataForm.SENDMENO" :attr="{ maxlength: 70 }" ></mt-field>
                </div>
                <span class="field-remark-dest">转账附言70字以内</span>
                <div class="field-action">
                    <mt-button @click="search()" :disabled="!submitStatus" size="large" type="primary">下一步</mt-button>
                </div>
                <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
                <selector-transfer-aging ref="selectorTransferAging" :options=" selectorTransferAging " ></selector-transfer-aging>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div class="two-dimension-code">
                <div class="two-dimension-code-items" @click= "payment()">
                    <div class="code-items-info">
                        <span>付款</span>
                        <span>扫一扫二维码</span>
                        <span>向对方转账</span>
                    </div>
                    <div class="code-img code-img-combinedShape"></div>
                </div>
                <div class="two-dimension-code-items" @click= "receivables()">
                    <div class="code-items-info">
                        <span>收款</span>
                        <span>显示我的二维码</span>
                        <span>向对方收钱</span>
                    </div>
                    <div class="code-img code-img-tranIcon"></div>
                </div>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
    </div>
	
</template>

<script>

import * as service from '@/services/public';
import * as serviceTransfer from '@/services/bank/internal-transfer';

export default {
    data() {
        return {
            selected: '1',
            submitStatus: true,
            dataForm: {
                paymentAccountList: [],
                checked: {},
                patternName: '实时到账',
                TRAN_TYPE: '0', // 转账方式 0-实时 1-次日
                COR_NAME: '', // 收款户名
                PAYEE_ACCOUNT: '', // 收款账号
                PAYER_ACCOUNT: '', // 付款账户
                CURRENCY: '0', // 付款币种 0-人民币
                AMOUNT: '', // 转账金额
                FEE: '', // 手续费
                SENDMENO: '', // 附言
                ISMOBILETRAN: '0', // 是否手机号转账 1-是 0-不是
                PAY_PWD: '', // 交易密码
                MSGCODE: '' // 短信验证码
            },
            selectorPaymentAccount: {
                title: '选择付款账号',
                type: 'payment',
                checked: {},
                showAddBtn: false,
                showWarn: true,
                showBankName: false,
                accountList: []
            },
            selectorTransferAging: {
                title: '选择到账时间',
                checked: '',
                instant: true,
                general: true,
                morrow: true
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.internalTransfer.search);
        this.dataForm = Object.assign(this.dataForm, this.$store.getters.transactionDemo);
        var dimensionCode = this.$ls.getItem('two-dimension-code-back');
        if(dimensionCode == 'code'){
            this.selected = '2';
            this.$ls.removeItem('two-dimension-code-back');
        }
        if(!this.dataForm.PAYER_ACCOUNT){
            // flagType 目前没数据 后面改为3
            service.getCommonAccountList({'flagType': '0'}).then((res) => {
                this.selectorPaymentAccount.accountList = res.LIST;
                this.selectorPaymentAccount.checked = res.LIST[0];
                this.dataForm.paymentAccountList = res.LIST;
                this.dataForm.checked = res.LIST[0];
                this.dataForm.PAYER_ACCOUNT = res.LIST[0].ACCOUNT;
            }, (err) => {
                console.log( err );
            });
        }else{
            this.selectorPaymentAccount.accountList = this.dataForm.paymentAccountList;
            this.selectorPaymentAccount.checked = this.dataForm.checked;
            this.dataForm.PAYER_ACCOUNT = this.dataForm.checked.ACCOUNT;
        }
    },
    mounted(){
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            // var _error = [];
            // if( this.$validate.isEmpty( _data.PAYER_ACCOUNT ) ) {
            //     _error.push( '银行卡号不能为空。' );
            // }
            // if( _error.length > 0 ) {
            //     this.$toast(_error[0]);
            //     return;
            // }
            // serviceTransfer.getInternalTransferPay({
            //     'TRAN_TYPE': this.dataForm.TRAN_TYPE,
            //     'COR_NAME': this.dataForm.COR_NAME,
            //     'PAYEE_ACCOUNT': this.dataForm.PAYEE_ACCOUNT,
            //     'PAYER_ACCOUNT': this.dataForm.PAYER_ACCOUNT,
            //     'CURRENCY': this.dataForm.CURRENCY,
            //     'AMOUNT': this.dataForm.AMOUNT,
            //     'FEE': this.dataForm.FEE,
            //     'SENDMENO': this.dataForm.SENDMENO,
            //     'ISMOBILETRAN': this.dataForm.ISMOBILETRAN
            // }).then((res) => {
            //     console.log(res);
            // }, (err) => {
            //     console.log( err );
            // });
    		var params = {
    			path: '/internal-transfer/pay',
    			query: {
    				id: _data.PAYER_ACCOUNT
    			}
    		};
            this.$router.push(params);
        },
        getFee () {
            serviceTransfer.getInternalTransferPay({
                'TRANCODE': '021001',
                'TRAN_AMT': this.dataForm.TRAN_AMOUNT,
                'ACCOUNT': this.dataForm.PAYMENT_ACCOUNT,
                'ACCT_NAME': 'test',
                'COR_INFO': this.dataForm.PAYEE_ACCOUNT,
                'COR_NAME': this.dataForm.PAYEE_NAME,
                'BANKCODE': this.dataForm.PAYEE_BANK_NO
            }).then((res) => {
                this.dataForm.TRAN_FEE = res.FEE;
                this.search();
            }, (err) => {
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.PAYER_ACCOUNT = data.item.ACCOUNT;
                this.dataForm.checked = data.item;
            }, ( data ) => {});
        },
        showSelectorTransferAging() {
            this.$refs.selectorTransferAging.show().then(( data ) => {
                this.selectorTransferAging.checked = data.item;
                this.dataForm.TRAN_TYPE = data.item.code;
                this.dataForm.patternName = data.item.text;
            }, ( data ) => {});
        },
        payment() {
            this.$router.push("/two-dimension-code/payment/index");
        },
        receivables() {
            this.$router.push("/two-dimension-code/receivables");
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.PAYER_ACCOUNT && item.AMOUNT && item.COR_NAME && item.PAYEE_ACCOUNT){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            var d = this.dataForm;
            this.$store.dispatch('updatePayee', {"name": d.COR_NAME, "account": d.PAYEE_ACCOUNT, "bankName": d.payeeBankName});
            this.$store.dispatch('updateInternalTransferSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearInternalTransferSearch');
            this.$store.dispatch('clearPayee');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.my-payee-img {
    position: relative;
    img {
        width: 80px;
        position: absolute;
        top: 55px;
        right: 10px;
    }
}
.filter-payee {
    margin-top: 20px;
}
.field-remark {
    margin-top: 84px !important;
}
.two-dimension-code {
    text-align: center;
   .two-dimension-code-items {
        width: 690px;
        height: 340px;
        margin-top: 60px;
        border-radius: 9px;
        display: inline-block;
    }
    .two-dimension-code-items:nth-child(1) {
        background-image: linear-gradient(49deg, #FF5722 0%, #FFA32F 100%);
        
    }
    .two-dimension-code-items:nth-child(2){
        background-image: linear-gradient(-130deg, #34B2FF 0%, #008FFF 100%);
    }
    .code-items-info {
        float: left;
        text-align: left;
        width: 50%;
        height: 100%;
        span {
            display: inline-block;
            opacity: 0.8;
            font-size: 12px;
            color: #FFFFFF;
            margin-left: 100px;
        }
        span:nth-child(1) {
            font-size: 24px;
            opacity: 1;
            color: #fff;
            padding-top: 100px;
            height: 66px;
            line-height: 66px;
        }
    }
    .code-img {
        float: right;
        margin: 106px 104px;
        height: 108px;
        width: 108px;
        background-repeat: no-repeat;
        background-size: 100%;
    }
    .code-img-combinedShape {
        background-image: url('../../../../assets/img/bank/transfer/combined-shape@2x.png');
        background-image: image-set(url('../../../../assets/img/bank/transfer/combined-shape@3x.png') 3x);
    }
    .code-img-tranIcon {
        background-image: url('../../../../assets/img/bank/transfer/tran-icon@2x.png');
        background-image: image-set(url('../../../../assets/img/bank/transfer/tran-icon@3x.png') 3x);
    }
}
.btn-seletor-payee
{
    background-color: #fff;
    .icon
    {
        background-image: url('../../../../assets/img/selector/icon-address-book.png');
    }
}
</style>



