<template>
	<div class="large-deposit-draw-detail">
		<mt-cell title="大额存单账户" :value="detail.number"></mt-cell>
		<mt-cell title="发行编号：" :value="detail.company"></mt-cell>
		<mt-cell title="发行机构：" :value="detail.name"></mt-cell>
		<mt-cell title="产品名称：" :value="detail.money1"></mt-cell>
		<mt-cell title="存单期限：" :value="detail.money2"></mt-cell>
		<mt-cell title="付息方式：" :value="detail.rate"></mt-cell>
		<mt-cell title="是否允许转让：" :value="detail.date"></mt-cell>
		<mt-cell title="存单本金：" :value="detail.date1"></mt-cell>
		<mt-cell title="存单利率：" :value="detail.date2"></mt-cell>
		<mt-cell title="提取支取利率：" :value="detail.status"></mt-cell>
		<mt-cell title="起息日期：" :value="detail.status"></mt-cell>
		<mt-cell title="到期日期：" :value="detail.status"></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary">返回</mt-button>
			<mt-button @click.native="submit" type="primary">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/large-deposit';

export default {
	data() {
		return {
			detail: {
				liveAccount: '',
				keepAccount: '',
				money: '',
				type: '',
				status: '',
				date: '',
				date1: '',
				date2: '',
				rate: ''
			}
		};
	},
	methods: {
		submit() {
			this.$router.push({name: 'LargeDepositDrawDraw'});
		},
		back() {
			history.back();
		},
		getDrawProductDetail(account) {
			service.getDrawProductDetail({
				account: account
			}).then(data => {
				this.detail = data.data.data;
				if(this.detail.keepType != 2){
					this.inputs.giveType = '1';
				}
			});
		}
	},
	created() {
		this.getDrawProductDetail(this.$route.params.account);
	}
};

</script>


