import ajax from '@/services/ajax';
import api from '@/services/api';

export function getAgriculturalCreditTransferIndex(data) {
    return ajax.postAfterLoginQuery( '021005', data, api.getAgriculturalCreditTransferIndex );
}

export function getAgriculturalCreditTransferFee(data) {
    return ajax.postAfterLoginQuery( '021005', data, api.getAgriculturalCreditTransferFee );
}

export function getAgriculturalCreditTransferBankCode(data) {
    return ajax.postAfterLoginUpdate( '010206', data, api.getAgriculturalCreditTransferBankCode );
}

export function getAgriculturalCreditTransferPay(data) {
    return ajax.postAfterLoginUpdate( '021005', data, api.getAgriculturalCreditTransferPay );
}

export function getAgriculturalCreditTransferSuccess(data) {
    return ajax.postAfterLoginQuery( '012025', data, api.getAgriculturalCreditTransferSuccess );
}



