<template>
    <div>
        <div class="two-dimension-code-receivables field-part">
            <span>安徽农金手机银行APP扫一扫，向我付款</span>
            <img src="../../../../../../assets/img/bank/QR_code.png">
            <span>二维码每五分钟自动刷新</span>
            <span @click = "refresh()">现在刷新</span>
        </div>
        <div class="field-part">
            <mt-cell label="收款人" :value=" dataForm.ACCTNAME "></mt-cell>
            <mt-cell title="收款账户" is-link @click.native=" showSelectorPaymentAccount() "
                :value="  dataForm.ACCOUNT || '请选择卡号' " is-link ></mt-cell>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
    </div>
</template>

<script>

import * as service from '@/services/public';
import * as serviceCode from '@/services/bank/two-dimension-code';

export default {
    data() {
        return {
            selectorPaymentAccount: {
                title: '选择付款账号',
                checked: {},
                showAddBtn: true,
                accountList: []
            },
            dataForm: {
                ACCOUNT: '',
                ACCTNAME: ''
            }
        };
    },
    created() {
        this.dataForm = Object.assign(this.dataForm, this.$store.state.internalTransfer.search);
        service.getCommonAccountList({'flagType': '0'}).then((res) => {
            this.selectorPaymentAccount.accountList = res.LIST;
            this.selectorPaymentAccount.checked = res.LIST[0];
            this.dataForm.ACCOUNT = res.LIST[0].ACCOUNT;
        }, (err) => {
            console.log( err );
        });
        this.dataForm.ACCTNAME = '邵珠利';
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$ls.setItem("two-dimension-code-back", "code");
            this.$router.go(-1);
        },
        refresh (){
            serviceCode.getTwoDimensionCodeRefresh({
                ACCOUNT: this.dataForm.ACCOUNT,
                ACCTNAME: this.dataForm.ACCTNAME
            }).then((res) => {
                console.log( res );
            }, (err) => {
                this.$alert(err.MESSAGE, {
                    btn: { text: '完成' }
                });
            });
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.ACCOUNT = data.item.ACCOUNT;
            }, ( data ) => {});
        }
    }
};
</script>


<style lang="scss">
.two-dimension-code-receivables {
    padding-top: 40px;
    height: 608px;
    background: #fff;
    text-align: center;
    img {
        height: 330px;
        width: 330px;
        margin-top: 40px;
    }
    span {
        display: block;
    }
    span:nth-child(1) {
        font-size: 16px;
    }
    span:nth-child(3) {
        font-size: 15px;
        margin-top: 40px;
    }
    span:nth-child(4) {
        font-size: 13px;
        color: #308CF8;
        margin-top: 20px;
    }
}
</style>



