<template>
    <div>
        <div class="success-info">
            <tip-page tipTitle="转账成功" tipMsg="实时转账"></tip-page>
            <transfer-success-detail 
                :payer='accountsDetail.payer' :payerAccount='accountsDetail.account' payerBank='' 
                :payee='accountsDetail.payeeName' :payeeAccount='accountsDetail.payeeAccount' :payeeBank='accountsDetail.payeeBankName' 
                :payeeMoney='accountsDetail.money' :remark='accountsDetail.remark' transferType='8'></transfer-success-detail>
        </div>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toIndex()">返回首页</mt-button>
        </div>
        <div class="cell-action">
            <mt-button size="large" @click = "toAccountIndex()">继续转账</mt-button>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            accountsDetail: []
        };
    },
    created() {
        this.accountsDetail = Object.assign({}, this.$store.state.largeAmountOfRemittance.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        toIndex() {
            this.$router.push('/');
        },
        toAccountIndex() {
            // this.$router.go(-3);
            this.$router.push('/accounts/index');
        }
    }
};
</script>


<style lang="scss">
.success-info {
    background: #fff;
}
</style>



